<template>
  <div class="row">
    <form @submit.prevent="onSubmit" class="col l6 m8 s12 offset-l3 offset-m2 offset-s0">
      <div class="row">
        <h5>3. Переработки и праздничные дни</h5>
      </div>
      <div class="overtime">
        <label>
          <input type="checkbox" class="filled-in" v-model="overtimeChecked" />
          <span>Были переработки</span>
        </label>
        <div class="input row" v-show="overtimeChecked">
          <div class="input-field col s9">
            <input id="shifts-over" type="text" class="validate" required v-model="overtime" />
            <label for="shifts-over">Количество смен</label>
            <span class="helper-text" data-error="Заполните это поле"></span>
          </div>
          <button type="button" class="btn btn-count red" @click="changeOvertime('down')">-</button>
          <button type="button" class="btn btn-count btn-plus" @click="changeOvertime('up')">+</button>
        </div>
      </div>
      <div class="overtime">
        <label>
          <input type="checkbox" class="filled-in" v-model="holidayChecked"/>
          <span>Работал в праздники</span>
        </label>
        <div class="input row" v-show="holidayChecked">
          <div class="input-field col s9">
            <input id="holidays-over" type="text" class="validate" required v-model="holidays" />
            <label for="holidays-over">Часов</label>
            <span class="helper-text" data-error="Заполните это поле"></span>
          </div>
          <button type="button" class="btn btn-count red" @click="changeHoliday('down')">-</button>
          <button type="button" class="btn btn-count btn-plus" @click="changeHoliday('up')">+</button>
        </div>
      </div>

      <ControlButtons linkBack="/shifts" nextText="Следующий шаг" />
    </form>
  </div>
</template>

<script>
import ControlButtons from "@/components/ControlButtons";
export default {
  name: "overtime",
  components: { ControlButtons },
  data: () => ({
    overtime: 0,
    holidays: 0,
    overtimeChecked: false,
    holidayChecked: false,
  }),
  mounted() {
    M.updateTextFields();
  },
  methods: {
    changeOvertime(type) {
      if (type === "down") this.overtime--;
      else this.overtime++;
    },
    changeHoliday(type) {
      if (type === "down") this.holidays--;
      else this.holidays++;
    },
    onSubmit(){
      let overtime = this.overtimeChecked ? this.overtime : 0
      let holidays = this.holidayChecked ? this.holidays : 0
      let overtimes = {
        overtime,
        holidays
      }
      this.$store.dispatch('setOvertimes', overtimes)
      this.$router.push("/other");
    }
  }
};
</script>

<style lang='scss' scoped>
label {
   display: block;
   margin-bottom: 1rem;
}
.btn-plus {
  float: right;
}
.btn-count {
  margin-top: 1rem;
}
</style>